.ImageBack {
  object-fit: cover;
  width: 100%;
  height: 30rem;
}

.BasicInfo {
  width: 62%;
  margin: 0 auto;

  .ParkName {
    font-family: "Playfair Display", serif;
    margin-top: 10px;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 2.3rem;
  }
  h3 {
    margin-bottom: 0px;
    font-size: 1.4rem;
  }
  .Desc {
    margin-top: 7px;
    margin-bottom: 30px;
    p {
      margin: 5px 0 5px 0;
      font-size: 0.95rem;
    }
  }
}
.WebcamInfo {
  width: 62%;
  margin: 0 auto 150px auto;
  h1 {
    color: rgb(26, 85, 145);
    margin: 100px auto 0 auto;
    font-weight: 600;
    font-size: 2.3rem;
  }
  h3 {
    margin: 0 auto 30px auto;
    font-weight: 300;
    font-size: 1.6rem;
  }
}
.webcamAll {
  display: flex;
  flex-wrap: wrap;
}

.webcams {
  width: 29rem;
  padding: 10px;
  margin: 0 auto;
  a {
    text-decoration-line: none;
    list-style: none;
    color: rgb(184, 154, 112);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0 auto 5px auto;
  }
  .noPicture {
    margin: 0 0 10px 0;
    background-color: rgb(245, 245, 245);
    font-size: 24px;
    text-align: center;
    padding: 104px 0 104px 0;
  }
  .webPictures {
    margin: 0 0 10px 0;
    object-fit: cover;
    width: 100%;
    height: 240px;
  }
  h4 {
    margin: 5px auto 0 auto;
    font-weight: 600;
  }
  p {
    margin: 7px auto 30px auto;
  }
}

@media (max-width: 480px) {
  .BasicInfo {
    width: 75%;
    .ParkName {
      letter-spacing: 0.5px;
    }
  }
  .WebcamInfo {
    width: 75%;
  }
  .webcams {
    width: 90%;
    .noPicture {
      text-align: center;
    }
    .webPictures {
      width: 100%;
      height: 241px;
    }
    .pictureContainer {
      margin: 0 auto;
      text-align: center;
    }
  }
}
